
@keyframes change{  
    0%{
        background-color: red;
    }
    100%{
        background-color: blue;
    }


}
#app4{}
#app4 .circle{
    border: 1px solid red; 
    width: 30vw;
    height: 30vw;
    border-radius: 50%;  
    
}
#app4 .circle.active{
    animation: change 1s infinite alternate linear;
}
